<template>
  <div class="p-login">
    <div class="l-form p-login__wrapper t-white" :class="{ 'p-login__wrapper--logo': isHomeMode, 'p-login__wrapper--sign': !isHomeMode }">
      <transition name="move-up" mode="out-in">
        <div v-if="showMode === 1" class="p-login__content p-login__content--login" key="login">
          <div class="p-login__1st p-login__logo"><div class="p-login__logo_icon"></div></div>
          <div class="p-login__2nd p-login__school">C D N I S</div>
          <div class="p-login__3rd p-login__tip">{{ $t('welcome') }}</div>
          <router-link class="p-login__4th p-login__button" :to="{ params: { mode: 'parent' } }" replace>{{ $t('login.sign') }}</router-link>
        </div>
        <div v-if="showMode === 2" class="p-login__content p-login__content--sign" key="sign">
          <div class="p-login__1st p-login__header">
            <router-link class="p-login__back" :to="{ name: 'login' }" replace>X</router-link>
          </div>
          <div class="p-login__1st p-login__nav">
            <router-link class="o-fixed-tab--white" :class="{ 'is-active': isParentMode }" :to="{ params: { mode: 'parent' } }" replace>{{ $t('login.parents') }}</router-link>
            <router-link class="o-fixed-tab--white" :class="{ 'is-active': isStaffMode }" :to="{ params: { mode: 'staff' } }" replace>{{ $t('login.teachers') }}</router-link>
          </div>
          <div v-if="isStaffMode">
            <form-field class="p-login__2nd" :label="$t('login.account')" :placeholder="$t('login.accountPlaceholder')" v-model="account"></form-field>
            <form-field class="p-login__3rd" type="password" :label="$t('common.pwd')" v-model="password"></form-field>
          </div>
          <div v-else>
            <form-field class="p-login__2nd c-form-field--select" :label="$t('sms.phone')" :placeholder="$t('login.phonePlaceholder')" v-model="mobile">
              <area-code @change="pick"></area-code>
            </form-field>
            <form-field v-if="usePwd" class="p-login__3rd" type="password" :label="$t('common.pwd')" v-model="password"></form-field>
            <form-field v-else class="p-login__3rd c-form-field--button" :label="$t('sms.vCode')" v-model="vCode">
              <sms :mobile="mobile" :areaCode="areaCode"></sms>
            </form-field>
          </div>
          <div class="p-login__4th u-clearfix">
            <router-link v-if="usePwd" class="p-login__forget u-underline" :to="{ name: 'pre-reset-pwd', params: { mode: isParentMode ? 'parent' : 'staff' } }">{{ $t('login.forget') }}</router-link>
            <div class="o-button o-button--submit" :class="{ 'is-active': isValid }" @click="sign">{{ $t('login.sign') }}</div>
          </div>
          <div class="p-login__soon p-login__error">{{ error }}</div>
          <div v-if="!isFirst && isParentMode" class="p-login__4th u-align-c p-login__toggle-tip">
            <div class="u-underline" @click="toggleLoginMode">{{ toggleTip }}</div>
          </div>
        </div>
      </transition>
    </div>
  </div>
</template>

<script>
import { mapActions } from 'vuex'
import * as Types from '@/store/mutation-types.js'
import formField from '@/components/FormField.vue'
import areaCode from '@/widgets/AreaCode.vue'
import sms from '@/widgets/Sms.vue'

let LoginState = {
  HOME: 0,
  PARENT: 1,
  STAFF: 2
}

export default {
  name: 'login',
  components: {
    formField,
    areaCode,
    sms,
  },
  data () {
    return {
      isFirst: false,
      showMode: '',
      mode: LoginState.HOME,
      requestMode: LoginState.HOME,
      signMode: 0, // 0 password, 1 vcode
      areaCode: '',
      mobile: '',
      vCode: '',
      account: '',
      password: '',
      error: ''
    }
  },
  computed: {
    isParentMode () {
      return this.mode === LoginState.PARENT
    },
    isStaffMode () {
      return this.mode === LoginState.STAFF
    },
    isHomeMode () {
      return this.mode === LoginState.HOME
    },
    usePwd () {
      return !this.isFirst && this.signMode === 0 || this.mode === LoginState.STAFF
    },
    toggleTip () {
      return this.usePwd ? this.$t('login.useVCode') : this.$t('login.usePwd')
    },
    mobileVal () {
      return this.areaCode && this.mobile ? this.areaCode + this.mobile : null
    },
    isValid () {
      return this.usePwd ? (this.isParentMode ? this.mobileVal : this.account) && this.password : this.mobileVal && this.vCode
    }
  },
  methods: {
    ...mapActions(['setRuntimeMode']),
    refreshMode () {
      this.$nextTick(() => {
        this.showMode = this.isHomeMode ? 1 : 2
      })
    },
    setMode (mode) {
      switch (mode) {
        case 'parent':
          mode = LoginState.PARENT
          this.$httpPost && this.$httpPost.setUrl('parent/login')
          break
        case 'staff':
          mode = LoginState.STAFF
          this.$httpPost && this.$httpPost.setUrl('staff/login')
          break
        default:
          mode = LoginState.HOME
          break
      }
      if (this.mode !== mode) {
        switch (this.mode) {
          case LoginState.PARENT:
            this.mobile = ''
            this.vCode = ''
            this.password = ''
            break
          case LoginState.STAFF:
            this.account = ''
            this.password = ''
            break
          default:
            break
        }
        this.stopLogin()
        this.mode = mode
      }
      this.refreshMode()
    },
    toggleLoginMode () {
      if (this.usePwd) {
        this.password = ''
      } else {
        this.vCode = ''
      }
      this.stopLogin()
      this.signMode = this.signMode === 0 ? 1 : 0
    },
    stopLogin () {
      this.error = ''
      this.$httpPost && this.$httpPost.cancel()
    },
    toSetPassword (mode, token) {
      this.$router.push({ name: 'set-pwd', params: { mode, token } })
    },
    changeRuntimmeMode (isBoot) {
      this.setRuntimeMode(isBoot ? 'boot' : 'normal')
    },
    sign () {
      if (this.isValid) {
        this.error = ''
        this.requestMode = this.mode
        if (!this.$httpPost) {
          this.$httpPost = this.$http.createPost(this.isParentMode ? 'parent/login' : 'staff/login', {
            opts: {
              lock: true
            },
            success: res => {
              let mode = this.requestMode === LoginState.PARENT ? 'parent' : 'staff'
              window.localStorage.setItem('hasLoggedIn', 'true')
              res.data.mode = mode
              this.$store.dispatch(Types.LOGIN, res.data)
              this.changeRuntimmeMode(res.data.isFirstLogin === 1 ? 'boot' : 'normal')
              if (res.data.isFirstLogin === 1) {
                this.toSetPassword(mode, res.data.token)
              } else {
                this.$router.push({ name: 'home', params: { mode } })
              }
            },
            fail: error => {
              this.error = error.message
            }
          })
        }
        switch (this.mode) {
          case LoginState.PARENT:
            this.$httpPost.request({
              account: this.mobileVal,
              passCode: this.usePwd ? this.password : this.vCode,
              type: this.usePwd ? 1 : 2
            })
            break
          case LoginState.STAFF:
            this.$httpPost.request({
              account: this.account,
              password: this.password
            })
            break
          default:
            break
        }
      }
    },
    pick (areaCode) {
      this.areaCode = areaCode
    }
  },
  beforeRouteUpdate (to, from, next) {
    this.setMode(to.params.mode)
    next()
  },
  created () {
    this.isFirst = !window.localStorage.getItem('hasLoggedIn')
    this.$nextTick(() => {
      this.setMode(this.$route.params.mode)
    })
  },
  beforeDestroy () {
    if (this.$httpPost) {
      this.$httpPost.cancel()
    }
  }
}
</script>

