<template>
  <div id="app">
    <router-view />
    <div v-if="showMask" class="requesting_mask">
      <img src="./components/loading.gif" />
    </div>
  </div>
</template>

<script>
import * as Event from '@/code/event.js'

export default {
  name: 'app',
  data () {
    return {
      showMask: false,
      maskId: -1
    }
  },
  created () {
    this.$eventBus.$on(Event.REQUESTING, id => {
      this.showMask = true
      this.maskId = id
    })
    this.$eventBus.$on(Event.REQUEST_FINISHED, id => {
      if (this.maskId === id) {
        this.maskId = -1
        this.showMask = false
      }
    })
  }
}
</script>

<style lang="scss">
#app {
  font-family: Verdana, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
  background: #fff;
}

.requesting_mask {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  padding-top: 80px;
  background: rgba(#000, 0.4);
  text-align: center;
  z-index: 999;
}
</style>
