function dateFtt (fmt, date) {
  let o = {
    'M+': date.getMonth() + 1,               //
    'd+': date.getDate(),                    //
    'h+': date.getHours(),                   //
    'm+': date.getMinutes(),                 //
    's+': date.getSeconds(),                 //
    'q+': Math.floor((date.getMonth() + 3) / 3), //
    'S': date.getMilliseconds()             //
  }
  if (/(y+)/.test(fmt)) {
    fmt = fmt.replace(RegExp.$1, (date.getFullYear() + '').substr(4 - RegExp.$1.length))
  }
  for (var k in o) {
    if (new RegExp('(' + k + ')').test(fmt)) {
      fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1 ) ? (o[k]) : (('00' + o[k]).substr(('' + o[k]).length)))
    }
  }
  return fmt
}

let today
// 
Mock.mock(`${ base_url }parent/child/signinrecord`, 'post', req => {
  let data = JSON.parse(req.body)
  if (data.page === 1) {
    today = Date.now()
  }
  let dayOffset = data.page - 1
  let array = []
  if (dayOffset <= 5) {
    let count = data.pageSize
    let hours = 1
    let time
    for (let i = 0; i < count; i++) {
      time = new Date(today)
      time.setHours(time.getHours() - dayOffset * count * hours - i * hours)
      array.push({
        'checkTimeStr': dateFtt('yyyy-MM-dd hh:mm:ss', time),
        'checkTime': time.getTime(),
        'type': Random.integer(1, 7)
      })
    }
  }
  return {
    'code': 200,
    'message': 'operate successfully',
    'data': array
  }
})

