let imgCache = {}

// 
Mock.mock(`${ base_url }common/attachment/upload`, 'post', req => {
  let code = 200 // Random.integer(200, 201)
  let reader = new FileReader()
  let realPath = window.URL.createObjectURL(req.body)
  imgCache[realPath] = {
    load: true
  }
  reader.onload = (data) => {
    imgCache[realPath].load = false
    let res = data.target || data.srcElement
    if (imgCache[realPath].child) {
      imgCache[realPath].child.headUrl = data.picImgPath
      save('kids', children)
      delete imgCache[realPath]
    } else if (imgCache[realPath].teacher) {
      imgCache[realPath].teacher.headUrl = data.picImgPath
      save('teacher', teacher)
      delete imgCache[realPath]
    } else {
      imgCache[realPath].img = res.result
    }
  }
  reader.readAsDataURL(req.body)
  return {
    'code': code,
    'message': code === 200 ? 'operate successfully' : 'operate failed',
    'data': {
      'attachmentId': Random.integer(1, 999),
      'realPath': realPath
    }
  }
})

