Mock.mock(`${ base_url }teacher/student/signinrecord`, 'post', req => {
  let data = JSON.parse(req.body)
  if (data.page === 1) {
    today = Date.now()
  }
  let dayOffset = data.page - 1
  let array = []
  if (dayOffset <= 5) {
    let count = data.pageSize
    let hours = 24
    let time
    for (let i = 0; i < count; i++) {
      time = new Date(today)
      time.setHours(time.getHours() - dayOffset * count * hours - i * hours)
      array.push({
        'checkTimeStr': dateFtt('yyyy-MM-dd hh:mm:ss', time),
        'checkTime': time.getTime(),
        'type': Random.integer(1, dayOffset <= 2 ? 2 : 3)
      })
    }
  }
  return {
    'code': 200,
    'message': 'operate successfully',
    'data': array
  }
})

