import Vue from 'vue'
import Vuex from 'vuex'
import * as Types from './mutation-types'
import http from '@/utils/http.js'

Vue.use(Vuex)

let userInfo = window.localStorage.getItem('smart_campus__user')
if (userInfo) {
  userInfo = JSON.parse(userInfo)
}

const SET_RUNTIME_MODE = 'SET_RUNTIME_MODE'

const CLEAR = 'CLEAR'

const UPDATE_RELATED_FIGURES = 'UPDATE_RELATED_FIGURES'

const UPDATE_USER = 'UPDATE_USER'

let Cache = {}
function getPost (url, props) {
  if (!Cache[url]) {
    Cache[url] = http.createPost(url, props)
  }
  return Cache[url]
}

function cancelPost () {
  for (let key in Cache) {
    Cache[key].cancel()
  }
  Cache = {}
}

const state = {
  runtimmeMode: 'normal',
  user: userInfo,
  isLogined: !!userInfo,
  figures: []
}

const getters = {
  getFigure: (state) => (data = {}) => {
    let id = data.id
    switch (data.mode) {
      case 'parent':
        return state.figures.find(figure => figure.studentId === id)
      case 'staff':
        return state.figures.find(figure => figure.token === id)
      default:
        return null
    }
  }
}

const mutations = {
  [Types.LOGIN] (state, payload) {
    state.user = payload
    state.isLogined = true
    window.localStorage.setItem('smart_campus__user', JSON.stringify(payload))
  },
  [Types.LOGOUT] (state) {
    state.user = null
    state.isLogined = false
    window.localStorage.removeItem('smart_campus__user')
  },
  [CLEAR] (state) {
    state.figures = []
    state.runtimmeMode = 'normal'
    cancelPost()
  },
  [UPDATE_RELATED_FIGURES] (state, payload) {
    state.figures = payload
  },
  [SET_RUNTIME_MODE] (state, payload) {
    state.runtimmeMode = payload
  },
  [UPDATE_USER] (state, payload) {
    console.log(payload)
    if (state.user) {
      Object.assign(state.user, payload)
      window.localStorage.setItem('smart_campus__user', JSON.stringify(state.user))
    }
  }
}

const actions = {
  [Types.LOGIN] ({ commit, dispatch }, payload) {
    commit(CLEAR)
    commit(Types.LOGIN, payload)
    dispatch('updateRelatedFigures')
  },
  [Types.LOGOUT] ({ commit }) {
    commit(CLEAR)
    commit(Types.LOGOUT)
  },
  updateRelatedFigures ({ state, commit }) {
    if (state.user) {
      switch (state.user.mode) {
        case 'parent':
          getPost('parent/children', {
            data: {
              token: state.user.token
            },
            opts: {
              retry: true
            },
            success: res => commit(UPDATE_RELATED_FIGURES, res.data)
          }).request()
          break
        case 'staff':
          commit(UPDATE_RELATED_FIGURES, [state.user])
          break
        default:
          break
      }
    }
  },
  setRuntimeMode ({ commit },  payload) {
    commit(SET_RUNTIME_MODE, payload)
  },
  updateUser ({ commit }) {
    if (state.user) {
      let post
      switch (state.user.mode) {
        case 'parent':
          post = getPost('parent/info', {
            opts: {
              retry: true
            },
            success: res => commit(UPDATE_USER, res.data)
          })
          break
        case 'staff':
          post = getPost('staff/info', {
            data: {
              token: state.user.token
            },
            opts: {
              retry: true
            },
            success: res => commit(UPDATE_USER, res.data)
          })
          break
        default:
          return
      }
      post.request({
        token: state.user.token
      })
    }
  }
}

let Store = new Vuex.Store({
  state,
  getters,
  mutations,
  actions,
  modules: { }
})

if (Store.state.isLogined) {
  Store.dispatch('updateRelatedFigures')
  Store.dispatch('updateUser')
}

export default Store
