<template>
  <div class="c-form-field" :class="{ 'is-invalid': !!error, 'is-active': isFocused }">
    <div class="c-form-field__label">{{ label }}</div>
    <div class="c-form-field__input_wrapper">
      <input :type="type" class="c-form-field__input" :placeholder="placeholder" :value="value"
        @input="$emit('input', $event.target.value)" @focus="isFocused = true" @blur="isFocused = false" />
      <slot></slot>
    </div>
    <div class="c-form-field__tip">{{ text }}</div>
  </div>
</template>

<script>
export default {
  name: 'form-field',
  props: {
    type: {
      type: String,
      default: 'text'
    },
    label: {
      type: String,
      default: ''
    },
    placeholder: {
      type: String,
      default: ''
    },
    error: {
      type: String,
      default: ''
    },
    tip: {
      type: String,
      default: ''
    },
    value: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      isFocused: false
    }
  },
  computed: {
    text () {
      return this.error || this.tip
    }
  }
}
</script>
