import Vue from 'vue'
import VueI18n from 'vue-i18n'
import en from './lang/en.js'
import zh_hk from './lang/zh-hk.js'

Vue.use(VueI18n)

const dateTimeFormats = {
  en: {
    week: {
      weekday: 'short'
    },
    month: {
      month: 'short'
    }
  },
  'zh-hk': {
    week: {
      weekday: 'short'
    },
    month: {
      month: 'narrow'
    }
  }
}

const i18n = new VueI18n({
  locale: window.localStorage.getItem('locale') || 'en',
  dateTimeFormats,
  messages: {
    en, // 
    'zh-hk': zh_hk // 
  }
})

export default i18n

export const langs = [
  {
    key: 'en',
    text: 'English'
  },
  {
    key: 'zh-hk',
    text: ''
  }
]
